.footer {
  padding-bottom: 20px;

  &__container {
    background: var(--light-grey-color) url(../images/footer-bg.avif) top right / auto no-repeat;
    border-radius: 50px;
    padding: 32px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px;
    border-radius: 22px;
    background: rgba(40, 40, 40, 0.60);
    backdrop-filter: blur(2px);
    margin-bottom: 60px;
  }

  &__contact {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      background: url(../images/phone.svg);
      top: 2px;
      left: -32px;
      width: 20px;
      height: 20px;
      transition: .5s ease-in;
    }
  }

  &__mail {
    position: relative;
    padding-right: 150px;

    &:after {
      content: "";
      position: absolute;
      background: url(../images/mail.svg);
      top: 3px;
      left: -40px;
      width: 20px;
      height: 20px;
      transition: .5s ease-in;
    }
  }

  &__box {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    align-items: start;
    margin-bottom: 32px;
  }


  .menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    &-item {
      a {
        cursor: text;
        font-size: 18px;
        font-weight: 400;
        color: var(--black-color);
      }
    }
  }

  .sub-menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 15px;

    li {
      a {
        cursor: pointer;
        font-size: 16px;
        color: var(--grey-color);
        font-weight: 300;
        transition: .4s ease-in;
      }
    }
  }

  &__risk {
    color: var(--grey-color);
    font-size: 16px;
    font-weight: 300;
    line-height: 1.4
  }

  &__copyright {
    color: var(--black-color);
    text-align: center;
  }
}