.conditions {
  padding-bottom: 150px;

  .container {
    padding-top: 20px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
    min-height: 425px;
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 160px 30px 30px;
    background-color: #3BA6B1;
    border-radius: 50px;
    gap: 25px;
    text-align: center;
    align-items: center;
  }

  p {
    max-width: 570px;
    text-align: center;
  }

  &__right {
    background: var(--light-grey-color) url(../images/conditions-bg.avif) 75% bottom / contain no-repeat;
    border-radius: 50px;
  }
}


.conditions-tariffs {
  padding-bottom: 150px;

  .title-l {
    text-align: center;
    margin-bottom: 25px;
  }

  &__subtitle {
    max-width: 750px;
    margin: 0 auto;
  }

  .text-s {
    text-align: center;
  }

  &__subtitle {
    color: var(--grey-color);
    text-align: left;
  }

  &__items {
    .text-m {
      color: var(--grey-color);
      text-align: left;
    }
  }

  &__cards {
    padding-top: 40px;
    padding-bottom: 50px;
    overflow: visible;

    &-pagination {
      display: none;
    }
  }

  &__card {
    transition: scale 0.4s ease-in;
    position: relative;
    padding: 32px;
    border-radius: 55px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;

    .text-s {
      color: var(--grey-color);
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  &__items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    margin-bottom: 85px;
    max-width: 370px;

    .text-m {
      margin: 0;
      text-align: left;
      list-style-type: disc;
    }
  }


  .mod-1 {
    background-color: var(--light-grey-color);
    transform: rotate(10deg);
    left: 50px;
    top: 40px;
  }

  .mod-2 {
    background-color: var(--purpur-light-color);
    transform: rotate(-10deg);
    top: 30px;
    left: 30px;
    z-index: 2;
  }

  .mod-3 {
    background-color: var(--light-grey-color);
    transform: rotate(10deg);
    top: 40px;
    z-index: 3;
  }
}


.conditions-market {
  padding-bottom: 150px;

  .container {
    padding-top: 20px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
    min-height: 495px;
  }

  &__left {
    padding: 64px 30px 30px;
    background: var(--brizz-color) url(../images/conditions-market-bg.avif) right bottom  / 450px 350px no-repeat;
    border-radius: 50px;
    text-align: left;

    .title-l {
      color: var(--white-color);
    }
  }

  &__right {
    padding: 65px 30px;
    background-color: var(--light-grey-color);
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    gap: 35px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: left;

    .text-m {
      color: var(--grey-color);
      margin-bottom: 0;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 15px;

    &:before {
      content: "";
      flex: 0 0 20px;
      height: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  .is-protected::before {
    background-image: url(../images/conditions-market-protected.svg);
  }

  .is-regulation::before {
    background-image: url(../images/conditions-market-regulation.svg);
  }

  .is-platform::before {
    background-image: url(../images/conditions-market-platform.svg);
  }

  .is-tools::before {
    background-image: url(../images/conditions-market-tools.svg);
  }

  .is-spread::before {
    background-image: url(../images/conditions-market-spread.svg);
  }

  .is-order::before {
    background-image: url(../images/conditions-market-order.svg);
  }

  .is-support::before {
    background-image: url(../images/conditions-market-support.svg);
  }

  .is-training::before {
    background-image: url(../images/conditions-market-training.svg);
  }

  .is-funds::before {
    background-image: url(../images/conditions-market-funds.svg);
  }

  .is-personal::before {
    background-image: url(../images/conditions-market-personal.svg);
  }
}


.conditions-intro {
  padding-bottom: 150px;

  .title-l {
    text-align: center;
  }

  &__cards {
    padding: 100px 10px 80px;

    &-pagination {
      display: none;
    }
  }

  &__card {
    cursor: default;
    transition: scale 0.4s ease-in;
    position: relative;
    min-height: 357px;
    padding: 32px;
    border-radius: 55px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;

    .text-m {
      color: var(--grey-color);
    }
  }

  &__avatars {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    img {
      width: 65px;
      height: 65px;
      border-radius: 50%;
      margin-left: -20px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  &__accounts {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    img {
      width: 64px;
      height: 64px;
      border-radius: 64px;
      margin-left: -25px;
    }
  }

  .mod-1 {
    background-color: var(--light-grey-color);
    transform: rotate(-10deg);
    left: 50px;

    &::before {
      content: " 1";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-2 {
    background-color: var(--purpur-light-color);
    transform: rotate(10deg);
    top: 40px;
    z-index: 2;

    &::before {
      content: " 2";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-3 {
    background-color: var(--light-grey-color);
    transform: rotate(-10deg);
    bottom: 30px;
    right: 10px;

    &::before {
      content: " 3";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-4 {
    background-color: var(--brizz-light-color);
    transform: rotate(10deg);
    top: 40px;
    right: 30px;

    &::before {
      content: " 4";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }
}


.conditions-invest {
  padding-bottom: 150px;

  &__container {
    background: var(--purpur-color) url(../images/invest-bg.avif) center center / cover no-repeat;
    border-radius: 54px;
    padding: 80px 250px 150px;
  }

  &__wrapper {
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 25px;

    .title-l {
      color: var(--white-color);
    }

    .text-m {
      margin-bottom: 20px;
      max-width: 510px;
    }
  }

  .btn {
    width: 700px;
  }
}
