.platform {
  padding-bottom: 150px;

  .container {
    padding-top: 20px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
    min-height: 425px;
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 160px 30px 30px;
    background-color: #3BA6B1;
    border-radius: 50px;
    gap: 25px;
    text-align: center;
    align-items: center;
  }

  p {
    max-width: 540px;
    text-align: center;
  }

  &__right {
    background: var(--light-grey-color) url(../images/platform-bg.avif) 75% bottom / contain no-repeat;
    border-radius: 50px;
  }
}


.platform-trader {
  padding-bottom: 150px;

  &__container {
    background-color: var(--light-grey-color);
    border-radius: 55px;
    max-height: 385px;
    position: relative;

    &:before {
      content: '';
      background: var(--brizz-color) url(../images/platform-trader.svg) center center/ 50px 45px no-repeat;
      position: absolute;
      top: -50px;
      right: 45px;
      width: 100px;
      height: 100px;
      border-radius: 22px;
      transform: rotate(10deg);
    }
  }

  &__wrapper {
    display: flex;
    gap: 60px;
    position: relative;
  }

  &__text {
    width: 770px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    padding-top: 65px;
    padding-left: 32px;
    gap: 25px;

    .title-l {
      color: var(--black-color);
    }

    .text-m {
      color: var(--grey-color);
    }

    .btn {
      width: 100%;
      display: block;
    }
  }

  &__img {
    img {
      margin-top: -45px;
    }
  }

  &__descr {
    p {
      max-width: 610px;
      margin-bottom: 10px;
    }
  }
}


.platform-mobile {
