.platform-trading {
  position: relative;
  z-index: 3;
  padding-bottom: 150px;

  &__container {
    min-height: 688px;
    background: var(--brizz-color) url(../images/about-trading-bg.avif) bottom right /auto no-repeat;
    border-radius: 55px;
    padding: 65px 40px 32px;
  }

  .title-l {
    color: var(--white-color);
    text-align: center;
    margin-bottom: 40px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: stretch;

    gap: 20px;
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    justify-content: space-between;
    padding: 25px;
    min-height: 245px;
    background-color: var(--light-grey-color);
    border-radius: 22px;
    gap: 30px 0;

    .text-m {
      color: var(--grey-color);
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}


.platform-intro {
  padding-bottom: 150px;

  .title-l {
    text-align: center;
  }

  &__cards {
    padding: 100px 10px 80px;

    &-pagination {
      display: none;
    }
  }

  &__card {
    cursor: default;
    transition: scale 0.4s ease-in;
    position: relative;
    min-height: 357px;
    padding: 32px;
    border-radius: 55px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;

    .text-m {
      color: var(--black-color);
    }
  }

  &__avatars {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    img {
      width: 65px;
      height: 65px;
      border-radius: 50%;
      margin-left: -20px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  &__accounts {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;

    img {
      width: 64px;
      height: 64px;
      border-radius: 64px;
      margin-left: -25px;
    }
  }

  .mod-1 {
    background-color: var(--light-grey-color);
    transform: rotate(-10deg);
    left: 50px;

    &::before {
      content: " 1";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-2 {
    background-color: var(--purpur-light-color);
    transform: rotate(10deg);
    top: 40px;
    z-index: 2;

    &::before {
      content: " 2";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-3 {
    background-color: var(--light-grey-color);
    transform: rotate(-10deg);
    bottom: 30px;
    right: 10px;

    &::before {
      content: " 3";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }

  .mod-4 {
    background-color: var(--brizz-light-color);
    transform: rotate(10deg);
    top: 40px;
    right: 30px;

    &::before {
      content: " 4";
      position: absolute;
      top: -15px;
      left: 30px;
      padding: 5px 20px;
      border-radius: 30px;
      color: var(--white-color);
      font-size: 16px;
      font-weight: 400;
      background: var(--purpur-color);
    }
  }
}


.platform-invest {
  padding-bottom: 150px;

  &__container {
    background: var(--purpur-color) url(../images/invest-bg.avif) center center / cover no-repeat;
    border-radius: 54px;
    padding: 80px 250px 150px;
  }

  &__wrapper {
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 25px;

    .title-l {
      color: var(--white-color);
    }

    .text-m {
      margin-bottom: 0;
    }
  }

  &__text {
    margin-bottom: 10px;
  }

  .btn {
    width: 700px;
  }
}
